 /* #Main Landing Section  */

 #header {
   width: 100%;
   height: 80vh;
   margin-top: 70px;
 }

 #header h1 {
   margin: 0 0 10px 0;
   font-size: 40px;
   font-weight: 700;
   color: black;
 }

 #header .brand-name {
   color: #3498db;
 }

 #header h2 {
   color: #484848;
   font-size: 24px;
 }

 #header .btn-get-started {
   font-weight: 500;
   font-size: 16px;
   letter-spacing: 1px;
   display: inline-block;
   padding: 10px 30px;
   border-radius: 50px;
   transition: 0.5s;
   color: #3498db;
   border: 2px solid #3498db;
   text-decoration: none;
 }

 #header .btn-get-started:hover {
   background: #3498db;
   color: #fff;
 }

 .header-img {
   text-align: right;
   
 }

 #header .animated {
   animation: up-down 2s ease-in-out infinite alternate-reverse both;
 }




.container{
	max-width: 1170px;
	margin:auto;
   }
.row{
	display: flex;
	flex-wrap: wrap;
}
ul{
	list-style: none;
}
.footer{
    position: relative;
    bottom: 0;
    width: 100%;
	background-color: #24262b;
    padding: 20px 0;
    
}
.footer-col{
   width: 25%;
   padding: 0 15px;
}
.footer-col h4{
	font-size: 18px;
	color: #ffffff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
}
.footer-col h4::before{
	content: '';
	position: absolute;
	left:0;
	bottom: -10px;
	background-color: #e91e63;
	height: 2px;
	box-sizing: border-box;
	width: 50px;
}
.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li a{
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}
.footer-col ul li a:hover{
	color: #ffffff;
	padding-left: 8px;
}
.footer-col .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: rgba(255,255,255,0.2);
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #ffffff;
	transition: all 0.5s ease;
}
.footer-col .social-links a:hover{
	color: #24262b;
	background-color: #ffffff;
}

