	font-size: 1rem;
	font-weight:normal;
	position: relative;
  }
  
  .menu_active {
	font-weight: bold;
	border-bottom: 2px solid blue;
  }
  
  .navbar li a:hover {
	color: white !important;
	text-shadow: 0 0.2rem 0.3rem orange;
	font-weight: bold;
	
  }

