/*responsive*/
@media(max-width: 767px){
  .footer-col{
    width: 50%;
    margin-bottom: 30px;
}
}
@media(max-width: 574px){
  .footer-col{
    width: 100%;
}
}


/* ----------- navbar css style starts ---------------*/

.navbar-bg {
	background-color: rgb(152, 130, 230);
	padding: 0.5rem 0;
	z-index: 99;
  
	position: relative;
  }

  .navbar-brand {
	font-size: 2.5rem;
	color: blue !important;
	font-weight: bold;
  }
  
  .navbar li {
	padding: 0 1rem;
  }
  
  .navbar li a {
	font-size: 1rem;
	font-weight:normal;
	position: relative;
  }
  
  .menu_active {
	font-weight: bold;
	border-bottom: 2px solid blue;
  }
  
  .navbar li a:hover {
	color: white !important;
	text-shadow: 0 0.2rem 0.3rem orange;
	font-weight: bold;
	
  }

